.dropdown-toggle {
  cursor: pointer;
}

.dropdown-toggle.generated:before {
  content: '\21d3';
  font-size: .8em;
}

.menumenu {
  list-style-type: none;
  display: flex;
}

.menumenu ul {
  list-style-type: none;
  display: block !important;
  left: 0;
  position: absolute;
  top: 100%;
  z-index: 9999;
  width: 240px;
}

.menumenu ul ul {
  top: 0;
}

.menumenu > li {
  position: relative;
}

.menumenu > li > ul {
  left: -6000px;
}

.menumenu > li > ul.sub-menu-in, .menumenu > li > ul.sub-menu-out {
  left: 0;
}

.menumenu .sub-menu-in,
.menumenu .sub-menu-out {
  left: 6000px;
}

.menumenu .sub-menu-in ul,
.menumenu .sub-menu-out ul {
  left: -6000px;
}

.menumenu .sub-menu-in {
  animation: SubMenuIn 0.5s cubic-bezier(0.77, 0, 0.175, 1);
}

.menumenu .sub-menu-out {
  animation: SubMenuOut 0.4s cubic-bezier(0.77, 0, 0.175, 1);
}

@keyframes SubMenuIn {
  0% {
    opacity: 0;
    transform: translate(0, 10px);
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes SubMenuOut {
  0% {
    opacity: 1;
    transform: translate(0, 0);
  }
  100% {
    opacity: 0;
    transform: translate(0, 10px);
  }
}
